import "./styles.css";
import { useState } from "react";

export default function App() {
  const [number, setNumber] = useState(0.0);
  const [value, setValue] = useState();
  const [addition, setAddition] = useState(false);
  const [substraction, setSubstraction] = useState(false);
  const [product, setProduct] = useState(false);
  const [division, setDivision] = useState(false);
  const [modulus, setModulus] = useState(false);

  var solve = function () {
    console.log("solved");
    console.log(addition);
    if (addition) {
      setNumber(value + number);
      setValue("");
    } else if (substraction) {
      setNumber(value - number);
      setValue("");
    } else if (product) {
      setNumber(value * number);
      setValue("");
    } else if (division) {
      setNumber(value / number);
      setValue("");
    } else if (modulus) {
      setNumber(value % number);
      setValue("");
    }
  };

  return (
    <div
      className="App"
      style={{
        display: "flex",
        justifyContent: "center",
        width: "100vw",
        height: "100vh",
        margin: "0",
        backgroundColor: "",
        alignItems: "center"
      }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          flexDirection: "column",
          width: "70%",
          backgroundColor: "",
          margin: "0",
          height: "70vh",
          borderRadius: "18px"
        }}
      >
        <div
          style={{
            backgroundColor: "#e6e8ed",
            width: "100%",
            height: "20%",
            borderRadius: "18px 18px 0 0",
            display: "flex",
            justifyContent: "flex-end",
            alignItems: "flex-end"
          }}
        >
          <div
            style={{
              backgroundColor: "#e6e8ed",
              width: "100%",
              height: "20%",
              borderRadius: "10px 10px 0 0",
              display: "flex",
              justifyContent: "center",
              alignItems: "flex-end"
            }}
          >
            <span>{value}</span>
          </div>
          <div
            style={{
              backgroundColor: "#e6e8ed",
              width: "100%",
              height: "20%",
              borderRadius: "18px 18px 0 0",
              display: "flex",
              justifyContent: "flex-end",
              alignItems: "flex-end"
            }}
          >
            <span>{number}</span>
          </div>
        </div>
        <div
          style={{
            backgroundColor: "",
            height: "20%",
            width: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center"
          }}
        >
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button onClick={() => setNumber(number * 10 + 7)}>7</button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button onClick={() => setNumber(number * 10 + 8)}>8</button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button onClick={() => setNumber(number * 10 + 9)}>9</button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button
              onClick={() => {
                setValue(number);
                setAddition(true);
                setNumber(0);
              }}
            >
              +
            </button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button
              onClick={() => {
                setValue(number);
                setSubstraction(true);
                setNumber(0);
              }}
            >
              -
            </button>
          </div>
        </div>
        <div
          style={{
            backgroundColor: "white",
            height: "20%",
            width: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center"
          }}
        >
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button onClick={() => setNumber(number * 10 + 4)}>4</button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button onClick={() => setNumber(number * 10 + 5)}>5</button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button onClick={() => setNumber(number * 10 + 6)}>6</button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button
              onClick={() => {
                setValue(number);
                setProduct(true);
                setNumber(0);
              }}
            >
              X
            </button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button
              onClick={() => {
                setValue(number);
                setDivision(true);
                setNumber(0);
              }}
            >
              /
            </button>
          </div>
        </div>
        <div
          style={{
            backgroundColor: "",
            height: "20%",
            width: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center"
          }}
        >
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button onClick={() => setNumber(number * 10 + 1)}>1</button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button onClick={() => setNumber(number * 10 + 2)}>2</button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button onClick={() => setNumber(number * 10 + 3)}>3</button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button
              onClick={() => {
                setValue(number);
                setModulus(true);
                setNumber(0);
              }}
            >
              %
            </button>
          </div>
          <div style={{ width: "20%", height: "100%", borderRadius: "5px" }}>
            <button onClick={solve}>=</button>
          </div>
        </div>
        <div
          style={{
            backgroundColor: "pink",
            height: "20%",
            width: "100%",
            borderRadius: "0 0 18px 18px"
          }}
        >
          <button
            style={{
              width: "100%",
              height: "100%",
              fontSize: "400%",
              borderRadius: "0 0 18px 18px"
            }}
            onClick={() => setNumber(number * 10 + 0)}
          >
            <b>0</b>
          </button>
        </div>
      </div>
    </div>
  );
}
